html {
  box-sizing: border-box; }

*,
::before,
::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-size: 15px;
  line-height: 1.2;
  font-weight: 400;
  font-family: "Lato", Arial, Helvetica, sans-serif;
  color: #3d3d3d;
  background-color: #f3f3f3; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap;
  /* added line */ }

[hidden] {
  display: none !important; }

.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  min-height: 46px;
  padding: 13px 23px;
  border: 0;
  border-radius: 20px;
  font-size: 16px;
  line-height: 1.25;
  font-weight: 700;
  font-family: inherit;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  background-color: #f22f49;
  transition: background-color 0.3s ease;
  outline: 0;
  cursor: pointer; }
  .button:disabled, .button--disabled {
    background-color: #919191;
    cursor: default;
    pointer-events: none; }
  .button:hover, .button:focus {
    background-color: #e00e2a; }

.button_invert {
  min-height: 42px;
  padding: 8px 22px;
  border-radius: 0;
  border: 2px solid #000;
  font-size: 17px;
  line-height: 1.2;
  color: #000;
  background-color: transparent; }
  .button_invert:hover, .button_invert:focus {
    color: #fff;
    background-color: #000; }

.input-text {
  width: 100%;
  display: block;
  height: 45px;
  padding: 13px 13px 12px;
  border: 1px solid transparent;
  border-radius: 5px;
  font: inherit;
  color: #000;
  background-color: #f3f3f3;
  outline: none; }
  .input-text:focus {
    border-color: #b6b6b6; }
  .input-text::placeholder {
    color: #a7a7a7; }

.input-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 54px;
  padding: 0;
  border: 0;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  background-color: #f10e34;
  outline: 0;
  cursor: pointer; }
  .input-button:disabled, .input-button--disabled {
    background-color: gray;
    cursor: default;
    pointer-events: none; }
  .input-button:hover, .input-button:focus {
    background-color: #c10b2a; }

.input-error {
  display: none;
  color: #f10e34; }

.input-error_visible {
  display: block; }

.form-control__message {
  margin-top: 8px; }

.header {
  color: #fff;
  background-color: #222222; }

.header__top {
  display: flex;
  justify-content: space-between;
  min-height: 115px; }

.header__user-menu {
  margin: 15px 0 20px;
  justify-content: flex-end; }

.header__logo {
  margin: 18px 0; }

.header__info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: flex-start;
  width: 100%;
  max-width: calc(100% - 25.39% - 39px);
  padding-left: 100px; }

.header__search {
  flex-grow: 1; }

.header__cart {
  flex-shrink: 0; }

.search-form__wrapper {
  display: flex; }

.search-form__button {
  flex-shrink: 0; }

.search-form__input {
  height: 37px;
  padding: 10px 9px 9px 26px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  background-color: #fff; }

.logo__img {
  display: block; }

.cart-link {
  display: flex;
  align-items: center;
  width: 106px;
  margin-left: 40px;
  color: #fff;
  text-decoration: none;
  transition: all 0.3s ease; }
  .cart-link:hover {
    color: #f10e34; }

.cart-link__title {
  padding-left: 8px;
  font-size: 11px;
  line-height: 1;
  text-transform: uppercase; }

.cart-link__price {
  margin-top: 6px;
  font-size: 14px;
  line-height: 1;
  font-weight: 700; }

.cart-link__icon-wrap {
  flex-shrink: 0;
  width: 37px;
  height: 37px;
  margin-right: 6px;
  border-radius: 50%;
  background-color: #f10e34; }

.cart-link:hover .cart-link__icon-wrap {
  background-color: #d90d2f; }

.nav {
  background-color: #f10e34; }

.nav__container {
  display: flex;
  justify-content: space-between;
  min-height: 51px; }

.nav__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  list-style: none;
  width: 100%;
  max-width: calc(100% - 25.39% - 39px);
  margin: 0;
  padding: 0; }

.nav__link {
  display: block;
  padding: 17px 0 16px;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;
  transition: color 0.3s ease; }
  .nav__link:hover {
    color: #ffc808; }

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px; }

.main__container {
  display: flex; }

.main__aside {
  flex-shrink: 0;
  width: 25.39%;
  margin-right: 39px; }

.main__content {
  flex-grow: 1;
  width: 100%; }

.aside__menu {
  margin-top: 0;
  margin-bottom: 33px; }

.aside__block:not(:first-child) {
  margin-top: 30px; }

