.aside-menu__item:not(:last-child) .aside-menu__link {
  border-bottom: 1px solid #eaeaea; }

.widget {
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.03);
  background-color: #fff;
  padding-left: 25px;
  padding-right: 25px; }

.widget__top {
  padding: 18px 0 16px;
  border-bottom: 1px solid #eaeaea; }

.widget__title {
  margin: 0; }

.widget__form {
  padding-top: 20px;
  padding-bottom: 30px; }

.widget__form-title {
  margin-top: 0;
  margin-bottom: 20px; }

.widget__form-control {
  margin-bottom: 23px; }

.title {
  font-size: 22px;
  line-height: 1.36;
  font-weight: 700;
  text-transform: uppercase;
  color: #000; }

.user-menu {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  list-style: none;
  width: 100%;
  padding: 0; }

.user-menu__item {
  display: flex;
  align-items: center; }
  .user-menu__item:not(:last-child):after {
    content: "|";
    margin: 0 18px; }

.user-menu__link {
  font-size: 12px;
  line-height: 1.5;
  color: #fff;
  text-decoration: none; }
  .user-menu__link:hover {
    color: #f22f49; }

.promo {
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.03); }

.promo__img {
  display: block;
  transition: opacity 0.3s ease; }

.promo__link:hover .promo__img {
  opacity: 0.9; }

.content__showcase {
  margin-bottom: 39px; }

.content__widget:not(:first-child) {
  margin-top: 30px; }

.showcase__background {
  display: flex;
  align-items: center;
  min-height: 376px;
  background-repeat: no-repeat;
  background-position: left top;
  /* ie11 */ }
  .showcase__background::after {
    content: "";
    min-height: inherit;
    font-size: 0; }

.showcase__text {
  max-width: 480px;
  padding: 45px 25px 30px;
  text-align: center; }

.showcase__title {
  margin: 0;
  font-size: 38px;
  line-height: 1.2;
  font-weight: 900;
  color: #303a7a;
  text-transform: uppercase;
  text-shadow: 0px 3px 7px rgba(0, 0, 0, 0.13); }

.showcase__lead {
  margin: 0;
  font-size: 60px;
  line-height: 0.97;
  font-weight: 900;
  color: #fff;
  text-transform: uppercase;
  text-shadow: 0px 3px 7px rgba(0, 0, 0, 0.13); }

.showcase__lead-subline {
  display: block;
  font-size: 22px;
  line-height: 1.5; }

.showcase__more {
  margin-top: 40px; }

.showcase__features {
  margin: 0; }

.features {
  display: flex;
  list-style: none;
  align-items: center;
  height: 79px;
  padding: 0;
  color: #fff;
  background-color: #f10e34; }

.features__item {
  width: 33.3333%;
  padding: 15px;
  text-align: center; }

.features__title {
  margin: 0;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.2em; }

.features__info {
  margin: 0; }
